.pay-with-near-ui-button {
  all: unset;
  width: 100%;
  padding: 24px 0;
  text-align: center;
  border-radius: 12px;
  background: #081b27;
  font-weight: 700;
  font-size: 18px;
  color: white;
  cursor: pointer;

  &:active {
    background: #0b293d;
  }

  &:disabled {
    background: #b5bbbe;
    cursor: not-allowed;
  }
}
