.pay-with-near-layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  overflow-x: hidden;

  &__header {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__logo {
    height: 40px;
  }
}

.pay-with-near-main {
  flex-grow: 1;
}

.pay-with-near-footer {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
  margin-bottom: 30px;
  max-width: 350px;

  &__support {
    color: #9ca4a9;
    font-weight: 700;
    font-size: 18px;
    text-decoration: underline;
  }
}
